import { request } from 'utils';

const prefix = '/api/categories';

export const getPracticeClassifications = ({
  label,
  labelInEnglish,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/PracticeClassification',
    method: 'GET',
    params: { label, labelInEnglish, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const deletePracticeClassifications = id =>
  request({
    prefix,
    url: `/PracticeClassification/${id}`,
    method: 'DELETE',
  });

export const createPracticeClassifications = ({ label, labelInEnglish, fromGrade, toGrade }) =>
  request({
    prefix,
    url: '/PracticeClassification',
    data: { label, labelInEnglish, fromGrade, toGrade },
    method: 'POST',
  });

export const updatePracticeClassifications = ({ id, label, labelInEnglish, fromGrade, toGrade }) =>
  request({
    prefix,
    url: `/PracticeClassification/${id}`,
    data: { id, label, labelInEnglish, fromGrade, toGrade },
    method: 'PUT',
  });

export const getPracticeClassificationsById = id =>
  request({
    prefix,
    url: `/PracticeClassification/${id}`,
    method: 'GET',
  });



